.story {
  position: relative;
  width: 100%;
  min-height: 100vh;
  overflow: hidden;
  background: linear-gradient(135deg, #524537, #000000);
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Playfair Display", serif;
  padding: 50px 20px; /* запас для маленьких экранов */
}

.particle-canvas {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
}

.story-content {
  position: relative;
  z-index: 2;
  max-width: 700px;
  text-align: center;
  padding: 50px 20px;
  color: #ffe7c2;
}

.story-title {
  font-size: 2.8rem;
  margin-bottom: 25px;
  letter-spacing: 1px;
  background: linear-gradient(90deg, #ffd98c, #ffb84d);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.story-text {
  font-size: 1.2rem;
  line-height: 1.8;
  margin-bottom: 40px;
  opacity: 0;
  animation: fadeIn 2s ease forwards 0.5s;
}

.story-invite {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 30px 25px;
  box-shadow: 0 8px 25px rgba(255, 180, 90, 0.3);
  transition: transform 0.5s ease, box-shadow 0.5s ease;
}

.story-invite:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 12px 35px rgba(255, 180, 90, 0.5);
}

/* Анимации */
.animate-fade {
  opacity: 0;
  animation: fadeIn 2s ease forwards 0.5s;
}

.animate-pop {
  opacity: 0;
  transform: scale(0.9);
  animation: popIn 1.8s ease 1s forwards;
}

/* Эффект печатающегося заголовка */
@keyframes typeWriter {
  from { width: 0; }
  to { width: 100%; }
}

.animate-type {
  overflow: hidden;
  white-space: nowrap;
  border-right: 2px solid #ffe7c2;
  width: 0;
  animation: typeWriter 3s steps(40, end) forwards;
}

@keyframes fadeIn {
  to { opacity: 1; }
}

@keyframes popIn {
  to { opacity: 1; transform: scale(1); }
}

/* ================== МЕДИА-ЗАПРОСЫ ================== */
/* Для планшетов */
@media (max-width: 1024px) {
  .story-title {
    font-size: 2.4rem;
  }
  .story-text {
    font-size: 1.1rem;
    line-height: 1.6;
  }
  .story-invite {
    padding: 25px 20px;
  }
}

/* Для телефонов */
@media (max-width: 768px) {
  .story {
    padding: 30px 15px;
  }
  .story-content {
    max-width: 90%;
    padding: 20px 10px;
  }
  .story-title {
    font-size: 2rem;
  }
  .story-text {
    font-size: 1rem;
    line-height: 1.4;
  }
  .story-invite {
    padding: 20px 15px;
  }
}

/* Для супер маленьких экранов (мобильные мини) */
@media (max-width: 480px) {
  .story-title {
    font-size: 1.6rem;
  }
  .story-text {
    font-size: 0.9rem;
  }
  .story-invite {
    padding: 15px 10px;
  }
}
